.app-inactive {
    display: -webkit-flex;
    display: flex;
    height: 100%;
}

.app-inactive-wrapper {
    max-width: 420px;
    margin: auto auto;
    text-align: center;
}

.app-inactive-image {
    background: url(/telegram-react/static/media/Manytabs_2x.7a71629a.png) no-repeat;
    background-size: 341px 262px;
    display: block;
    width: 341px;
    height: 262px;
    margin: 0 auto;
}

.app-inactive-title {
    font-size: 18px;
    margin-top: 20px;
    margin-bottom: 10px;
    font-weight: 500;
    line-height: 1.1;
}

.app-inactive-description {
    color: #666;
    font-size: 15px;
    line-height: 150%;
}

.app-inactive-actions {
    margin: 20px;
}

